import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Form, Button, Alert } from 'react-bootstrap';
import { setEmailConfig } from '../services/api';
import { getOpenAIKey, saveOpenAIKey } from '../services/localData';
import './Settings.css';

function Settings() {
  const [openaiKey, setOpenaiKey] = useState('');
  const [openaiMessage, setOpenaiMessage] = useState(null);
  
  // EmailJS
  const [emailjsId, setEmailjsId] = useState('');
  const [serviceId, setServiceId] = useState('');
  const [templateId, setTemplateId] = useState('');
  const [emailMessage, setEmailMessage] = useState(null);
  
  useEffect(() => {
    // 
    const savedOpenAIKey = getOpenAIKey();
    if (savedOpenAIKey) {
      setOpenaiKey('************' + savedOpenAIKey.slice(-4));
    }
    
    // 
    const savedEmailSettings = localStorage.getItem('email_settings');
    if (savedEmailSettings) {
      try {
        const settings = JSON.parse(savedEmailSettings);
        if (settings.emailjsId) setEmailjsId('************' + settings.emailjsId.slice(-4));
        if (settings.serviceId) setServiceId(settings.serviceId);
        if (settings.templateId) setTemplateId(settings.templateId);
      } catch (e) {
        console.error('Error loading email settings:', e);
      }
    }
  }, []);
  
  const handleOpenAISave = (e) => {
    e.preventDefault();
    try {
      if (!openaiKey || openaiKey.startsWith('******')) {
        setOpenaiMessage({
          type: 'danger',
          text: 'API'
        });
        return;
      }
      
      saveOpenAIKey(openaiKey);
      setOpenaiMessage({
        type: 'success',
        text: 'OpenAI API'
      });
      
      // API
      setOpenaiKey('************' + openaiKey.slice(-4));
      
      setTimeout(() => {
        setOpenaiMessage(null);
      }, 3000);
    } catch (error) {
      setOpenaiMessage({
        type: 'danger',
        text: `: ${error.message}`
      });
    }
  };
  
  const handleEmailSave = async (e) => {
    e.preventDefault();
    try {
      setEmailMessage(null);
      
      // 
      if (!emailjsId || emailjsId.startsWith('******')) {
        setEmailMessage({
          type: 'danger',
          text: 'EmailJS Public Key'
        });
        return;
      }
      
      if (!serviceId) {
        setEmailMessage({
          type: 'danger',
          text: 'EmailJS Service ID'
        });
        return;
      }
      
      if (!templateId) {
        setEmailMessage({
          type: 'danger',
          text: 'EmailJS Template ID'
        });
        return;
      }
      
      // 
      const config = {
        emailjsId: emailjsId.startsWith('******') ? 
          JSON.parse(localStorage.getItem('email_settings')).emailjsId : 
          emailjsId,
        serviceId: serviceId,
        templateId: templateId
      };
      
      // 
      setEmailMessage({
        type: 'info',
        text: '...'
      });
      
      console.log(':', {
        serviceId: config.serviceId,
        templateId: config.templateId,
        emailjsIdLength: config.emailjsId.length
      });
      
      // EmailJS
      if (!window.emailjs) {
        console.error('EmailJS SDK not loaded');
        setEmailMessage({
          type: 'danger',
          text: 'EmailJS'
        });
        return;
      }
      
      // EmailJS
      try {
        console.log('EmailJS...');
        window.emailjs.init(config.emailjsId);
        console.log('EmailJS');
      } catch (e) {
        console.error('EmailJS:', e);
        setEmailMessage({
          type: 'danger',
          text: `EmailJS: ${e.message || 'Public Key'}`
        });
        return;
      }
      
      const result = await setEmailConfig(config);
      
      if (result.success) {
        setEmailMessage({
          type: 'success',
          text: ''
        });
        
        // EmailJS ID
        if (!emailjsId.startsWith('******')) {
          setEmailjsId('************' + emailjsId.slice(-4));
        }
        
        // 
        const templateVerificationResult = await verifyEmailTemplate(config);
        if (!templateVerificationResult.valid) {
          setEmailMessage({
            type: 'warning',
            text: `: ${templateVerificationResult.message}`
          });
        }
        
        setTimeout(() => {
          if (emailMessage && emailMessage.type === 'success') {
            setEmailMessage(null);
          }
        }, 3000);
      }
    } catch (error) {
      console.error(':', error);
      setEmailMessage({
        type: 'danger',
        text: `: ${error.message || ''}`
      });
    }
  };
  
  /**
   * 
   */
  const verifyEmailTemplate = async (config) => {
    try {
      // serviceIdtemplateId
      // 
      const testParams = {
        to_email: "test@example.com",
        recipient: "test@example.com",
        user_email: "test@example.com",
        to_name: "Test User",
        from_name: 'arXiv',
        subject: "",
        message: "",
        content: "<p></p>",
        papers: "<p></p>"
      };
      
      try {
        await window.emailjs.send(
          config.serviceId,
          config.templateId,
          testParams
        );
        return { valid: true };
      } catch (error) {
        console.log(':', error);
        
        // "recipient""to_email"
        if (error.text && (error.text.includes('recipient') || error.text.includes('to_email'))) {
          return { 
            valid: false, 
            message: 'EmailJS{{to_email}}{{recipient}}' 
          };
        }
        
        // 
        return { valid: true };
      }
    } catch (e) {
      console.error(':', e);
      return { valid: true }; // 
    }
  };
  
  const clearSettings = () => {
    if (window.confirm('API')) {
      localStorage.removeItem('openai_key');
      localStorage.removeItem('email_settings');
      setOpenaiKey('');
      setEmailjsId('');
      setServiceId('');
      setTemplateId('');
      setOpenaiMessage({
        type: 'success',
        text: ''
      });
      setEmailMessage(null);
    }
  };
  
  return (
    <Container className="my-4">
      <h1></h1>
      <p className="text-muted">
        API
      </p>
      
      <Row className="mt-4">
        <Col md={6}>
          <Card className="mb-4">
            <Card.Header>OpenAI API</Card.Header>
            <Card.Body>
              <p>
                OpenAI APIAI
                APIOpenAI API
              </p>
              {openaiMessage && (
                <Alert variant={openaiMessage.type}>
                  {openaiMessage.text}
                </Alert>
              )}
              <Form onSubmit={handleOpenAISave}>
                <Form.Group className="mb-3">
                  <Form.Label>OpenAI API</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="sk-..." 
                    value={openaiKey}
                    onChange={(e) => setOpenaiKey(e.target.value)}
                  />
                  <Form.Text className="text-muted">
                    <a href="https://platform.openai.com/api-keys" target="_blank" rel="noreferrer">OpenAI</a>API
                  </Form.Text>
                </Form.Group>
                <Button variant="primary" type="submit">
                  API
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={6}>
          <Card className="mb-4">
            <Card.Header>EmailJS</Card.Header>
            <Card.Body>
              <p>
                EmailJS<a href="https://www.emailjs.com/" target="_blank" rel="noreferrer">EmailJS</a>
                ID
              </p>
              {emailMessage && (
                <Alert variant={emailMessage.type}>
                  {emailMessage.text}
                </Alert>
              )}
              <Form onSubmit={handleEmailSave}>
                <Form.Group className="mb-3">
                  <Form.Label>EmailJS Public Key</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="EmailJS Public Key" 
                    value={emailjsId}
                    onChange={(e) => setEmailjsId(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label>Service ID</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="service_xxxxxx" 
                    value={serviceId}
                    onChange={(e) => setServiceId(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label>Template ID</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="template_xxxxxx" 
                    value={templateId}
                    onChange={(e) => setTemplateId(e.target.value)}
                  />
                </Form.Group>
                <Button variant="primary" type="submit">
                  
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      <div className="d-flex justify-content-center mt-3 mb-5">
        <Button variant="danger" onClick={clearSettings}>
          
        </Button>
      </div>
    </Container>
  );
}

export default Settings; 