// 
export const getUserSubscriptions = () => {
  try {
    const subscriptions = localStorage.getItem('user_subscriptions');
    return subscriptions ? JSON.parse(subscriptions) : [];
  } catch (error) {
    console.error('Failed to get user subscriptions:', error);
    return [];
  }
};

// 
export const saveUserSubscription = (subscription) => {
  try {
    let subscriptions = getUserSubscriptions();
    
    // 
    const existingIndex = subscriptions.findIndex(sub => sub.email === subscription.email);
    
    if (existingIndex >= 0) {
      // 
      const existing = subscriptions[existingIndex];
      const allKeywords = [...new Set([...existing.keywords, ...subscription.keywords])];
      const allCategories = [...new Set([...existing.categories, ...subscription.categories])];
      
      subscriptions[existingIndex] = {
        ...existing,
        keywords: allKeywords,
        categories: allCategories,
        lastUpdated: new Date().toISOString()
      };
    } else {
      // 
      subscriptions.push({
        ...subscription,
        id: Date.now().toString(),
        created: new Date().toISOString(),
        lastUpdated: new Date().toISOString()
      });
    }
    
    localStorage.setItem('user_subscriptions', JSON.stringify(subscriptions));
    return true;
  } catch (error) {
    console.error('Failed to save user subscription:', error);
    return false;
  }
};

// 
export const deleteUserSubscription = (subscriptionId) => {
  try {
    let subscriptions = getUserSubscriptions();
    subscriptions = subscriptions.filter(sub => sub.id !== subscriptionId);
    localStorage.setItem('user_subscriptions', JSON.stringify(subscriptions));
    return true;
  } catch (error) {
    console.error('Failed to delete user subscription:', error);
    return false;
  }
};

// 
export const getPaperHistory = () => {
  try {
    const history = localStorage.getItem('paper_history');
    return history ? JSON.parse(history) : {};
  } catch (error) {
    console.error('Failed to get paper history:', error);
    return {};
  }
};

// 
export const addPapersToHistory = (email, papers) => {
  try {
    let history = getPaperHistory();
    
    if (!history[email]) {
      history[email] = [];
    }
    
    // 
    const paperLinks = papers.map(paper => paper.link);
    history[email] = [...new Set([...history[email], ...paperLinks])];
    
    localStorage.setItem('paper_history', JSON.stringify(history));
    return true;
  } catch (error) {
    console.error('Failed to add papers to history:', error);
    return false;
  }
};

// 
export const isPaperInHistory = (email, paperLink) => {
  try {
    const history = getPaperHistory();
    return history[email] && history[email].includes(paperLink);
  } catch (error) {
    console.error('Failed to check paper history:', error);
    return false;
  }
};

/**
 * OpenAI API
 * @returns {string|null} - OpenAI API
 */
export const getOpenAIKey = () => {
  try {
    console.log('Retrieving OpenAI API key from localStorage');
    const key = localStorage.getItem('openai_key');
    
    if (!key) {
      console.log('No OpenAI API key found in localStorage');
      return null;
    }
    
    console.log('OpenAI API key found, length:', key.length);
    return key;
  } catch (e) {
    console.error('Error retrieving OpenAI API key:', e);
    return null;
  }
};

/**
 * OpenAI API
 * @param {string} key - OpenAI API
 */
export const saveOpenAIKey = (key) => {
  try {
    console.log('Saving OpenAI API key, length:', key.length);
    
    // 
    if (!key || typeof key !== 'string' || key.length < 20) {
      console.error('Invalid OpenAI API key format');
      throw new Error('API');
    }
    
    // OpenAI (sk-...)
    if (!key.startsWith('sk-')) {
      console.warn('OpenAI API key does not start with sk-');
      // 
    }
    
    localStorage.setItem('openai_key', key);
    console.log('OpenAI API key saved successfully');
    return true;
  } catch (e) {
    console.error('Error saving OpenAI API key:', e);
    throw e;
  }
};

/**
 * 
 * @returns {Object|null} - 
 */
export const getEmailSettings = () => {
  try {
    const settings = localStorage.getItem('email_settings');
    if (!settings) {
      return null;
    }
    return JSON.parse(settings);
  } catch (e) {
    console.error('Error getting email settings:', e);
    return null;
  }
};

// 
export const isConfigured = () => {
  const openaiKey = getOpenAIKey();
  const emailSettings = getEmailSettings();
  
  return Boolean(openaiKey && emailSettings && emailSettings.email && emailSettings.password);
};

// 
export const refreshRecommendations = async (subscription) => {
  try {
    const { email, keywords, categories } = subscription;
    
    // API
    const { testKeywordMatch } = await import('./api');
    
    // 
    const matchedPapers = [];
    
    // 
    for (const category of categories) {
      console.log(` ${category} ...`);
      
      // OpenAI
      const papers = await testKeywordMatch(keywords, false, category);
      
      // 
      const newPapers = papers.filter(paper => !isPaperInHistory(email, paper.link));
      
      // 
      matchedPapers.push(...newPapers);
    }
    
    if (matchedPapers.length > 0) {
      // 
      let subscriptions = getUserSubscriptions();
      const index = subscriptions.findIndex(s => s.id === subscription.id);
      
      if (index !== -1) {
        subscriptions[index] = {
          ...subscriptions[index],
          recommendations: matchedPapers,
          lastUpdated: new Date().toISOString()
        };
        
        localStorage.setItem('user_subscriptions', JSON.stringify(subscriptions));
      }
    }
    
    return { 
      success: true, 
      message: ` ${matchedPapers.length} `, 
      papers: matchedPapers 
    };
  } catch (error) {
    console.error(':', error);
    return { 
      success: false, 
      message: error.message || '', 
      papers: [] 
    };
  }
}; 